.currencies {
  padding: 1.364rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: 23.18rem;
}

.currencies__search {
  border: 1px solid #4b666e;
  outline: none;
  font-size: 1rem;
  font-family: 'Raleway';
  border-radius: 5px;
  margin: 0 0 20px 0;
  width: 100%;
}

.currencies-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.currencies-list__item {
  padding: 0.45rem;
  font-size: 0.82rem;
  border: 1px solid #4b666e;
  border-radius: 5px;
  margin: 0 0 4px 0;
}



